form {
    width: 90%;
    max-width: 300px;
    background: #0f054cff;
    padding: 3rem 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: .3rem;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 1.6rem;
    color: #ededed;
    margin-bottom: 2rem;
    letter-spacing: .1rem;
}

textarea {
    background: #EBEFF2;
    border-radius: .6rem;
    color: #080328 ;
    /* box-shadow: 0  1rem 1rem #495B73; */
    font-size: 1.4rem;
    letter-spacing: .1rem;
    text-align: center;
    padding: 1.2rem;
    height: 8rem;
    width: calc(100% - 2rem);
    border: .15rem solid #080328;    
}

textarea::placeholder {
    color: #b3b0b0;
    font-family: 'Popins', sans-serif ;
    font-weight: 400;
    
}

textarea:focus {
    outline-color: #08e6ea;
}

.TodoFormButtonContainer {
    margin-top: 1.4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoFormButton {
    cursor: pointer;
    display: inline-block;
    font-size: 1.4rem;
    font-weight: bold;
    color: #ededed;
    font-weight: 400;
    width: 6rem;
    height: 3rem;
    border-radius: .6rem;
    border: none;
    font-family: 'Popins', sans-serif ;
}

.TodoFormButtonAdd {
    background: #fa154b;
    color: #ededed;
    font-weight: bold;
    /* box-shadow: 0 .2rem .5rem #72A9F2; */
}

.TodoFormButtonCancel {
    background: transparent;
    border: .15rem solid #fa154b;
    font-weight: bold;
    /* box-shadow: 0 .2rem .5rem #72A9F2; */
}

