@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');


body {
  margin: 0;
  padding: 0;
  background: #cdccf8ff;
  color: #273440;
  font-family: 'Popins', sans-serif ;
}

#root {
  margin: 0 2.4rem;
  position: relative;
  min-height: 100vh;
}

@media(max-width: 300px) and (max-width: 462px){
  #root {
      display: flex;
      width: 90vw;
      font-size: 20%;
      justify-content: center;
      align-items: center;
  }
}

